<template>
    <div class="approvals-manager">
        <tile
            v-show="view == 'grid'"
            :actions="gridActions"
            :loading="loading"
            icon="icon-user-check"
            title="Delegate Approvals">
            <div class="rules-list">
                <div class="section rules-wrap">
                    <grid
                        :columns="gridColumns"
                        :data="rules"
                        no-data-text="There are no rules defined">=
                    </grid>
                </div>
            </div>
        </tile>
        <tile
            v-show="view === 'edit'"
            :icon="newRule ? 'icon-plus-round' : 'icon-pencil'"
            :title="newRule ? 'Add an Approval Rule' : 'Edit Approval Rule'">
            <div class="section">
                <div class="section-body">
                    <div class="default-form">
                        <div class="form-row">
                            <label>From Date:</label>
                            <datepicker v-model="dateFrom" allow-null></datepicker>
                        </div>
                        <div class="form-row">
                            <label>To Date:</label>
                            <datepicker v-model="dateTo" allow-null></datepicker>
                        </div>
                        <div class="form-row">
                            <label>Type:</label>
                            <select v-model="rule.Type">
                                <option value="Share">Share</option>
                                <option value="Delegate">Delegate</option>
                            </select>
                        </div>
                        <div class="form-row">
                            <label>Alternate Approver:</label>
                            <div class="alt-approver-stack" v-if="approvers && approverList">
                                <div class="alt-approver-box">
                                    <auto-complete :inputList="approverList"
                                                   :defaultSelected="defaultAlternative"
                                                   :optionName="'display'" @selected="selectApprovers">
                                    </auto-complete>
                                </div>
                                <div class="filter-buttons">
                                    <button
                                        :class="{ active: showInferiors }"
                                        class="button icon-users-old"
                                        title="Show My Non-Managers"
                                        @click="toggleInferiors">
                                    </button>
                                </div>
                            </div>
                        </div>
                        <div class="form-row">
                            <label>Notification Area:</label>
                            <select v-model="rule.Area">
                                <option
                                    v-for="(aname, index) in areas"
                                    :key="index"
                                    :value="index">
                                    {{ aname }}
                                </option>
                            </select>
                        </div>
                        <div v-if="submitting" class="form-row row-bottom loader-box"></div>
                        <div v-else class="form-row row-bottom">
                            <button class="button green" @click="save()">
                                <span class="b-icon icon-save"></span><span>Save</span>
                            </button>
                            <button class="button grey ml-2" @click="cancel()">
                                <span class="b-icon icon-cancel"></span><span>Cancel</span>
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        </tile>

    </div>
</template>

<script>
import Vue from "vue";
import moment from "moment";
import {mapActions} from "vuex";
import Remote from '../remote';
import _ from 'lodash';
import AutoComplete from "../components/AutoComplete";

export default {
    components: {
        AutoComplete
    },
    data() {
        return {
            rules: [],
            view: "grid",
            loading: true,
            submitting: false,
            newRule: true,
            rule: {},
            approvers: null, // []
            approverEl: null,
            selectedApprover: null,
            defaultAlternative: null,
            showInferiors: null,
            dateFrom: null,
            dateTo: null,
            areas: ["All", "Leave", "Timesheet", "Mileage Claims", "Clever Forms"],
            gridColumns: [
                {
                    name: "DateFrom",
                    header: "Date From",
                    type: "date",
                    format(value) {
                        return moment(value).format("ll");
                    }
                },
                {
                    name: "DateTo",
                    header: "Date To",
                    type: "date",
                    format(value) {
                        return moment(value).format("ll");
                    }
                },
                {
                    name: "Type",
                    header: "Type"
                },
                {
                    name: "ApproverName",
                    header: "Alternate Approver"
                },
                {
                    name: "AreaName",
                    header: "Notification Area"
                },
                {
                    header: "",
                    buttons: [
                        {
                            mark: "small",
                            color: "orange",
                            icon: "icon-pencil",
                            text: "Edit",
                            click: rule => {
                                this.setDefaultAlternative(rule)
                                this.editRule(rule);
                                this.newRule = false;
                                this.view = "edit";
                            }
                        },
                        {
                            mark: "small",
                            color: "red",
                            icon: "icon-trash",
                            text: "Delete",
                            click: (rule, start, stop) => {
                                start();
                                this.delete(rule, stop);
                            }
                        }
                    ]
                }
            ],
            gridActions: [
                {
                    label: "Add rule",
                    icon: "icon-plus",
                    color: "blue",
                    click: () => {
                        this.newRule = true;
                        this.editRule();
                        this.view = "edit";
                    }
                }
            ]
        };
    },
    mounted() {
        this.getData();
    },
    computed: {
        approverList() {
            if (!this.approvers) return [];

            if (!this.showInferiors) {
                return this.approvers.filter(a => {
                    return a.IsDirect !== null
                })
            } else {
                return this.approvers.filter(a => {
                    return a.IsDirect === null
                })
            }
        }
    },

    methods: {
        ...mapActions(["addToast", "showRequestError"]),
        setDefaultAlternative(approver) {
            this.selectedApprover = approver

            let inCurrentList = this.approverList.find(apps => {
                return apps.EmployeeNo === approver.ApproverId
            })
            if (!inCurrentList) {
                this.showInferiors = !this.showInferiors

                let inCurrentList2 = this.approverList.find(apps => {
                    return apps.EmployeeNo === approver.ApproverId
                })
            }
            this.defaultAlternative = approver.ApproverId + ' ' + approver.ApproverName


        },
        getData() {
            this.loading = true;
            this.$ajax
                .post(Remote.approvalsGetDataAPI)
                .then(resp => {
                    let data = this.parseResponse(resp, "Couldn't retrieve rules");
                    if (!data) return;

                    this.rules = data.Rules || [];
                    this.rules.forEach((r, i) => {
                        r.AreaName = this.areas[r.Area];
                    });

                    this.approvers = data.Approvers.map(approver => {
                        approver.display = approver.EmployeeNo + ' ' + approver.Name
                        return approver
                    });
                })
                .catch(resp => {
                    this.showRequestError({
                        resp,
                        title: "Couldn't retrieve rules"
                    });
                })
                .then(() => {
                    this.loading = false;
                });
        },
        parseResponse(resp, errTitle) {
            let body = resp;
            if (resp.data) {
                body = resp.data
            }

            if (typeof body == "string") {
                return JSON.parse(body);
            }
            else if (body.Approvers) {
                return body;
            } else {
                this.showRequestError({
                    body,
                    title: errTitle
                });
            }
        },
        selectApprovers(approver) {
            this.selectedApprover = approver
            // this.rule.Area = area
        },
        editRule(rule) {
            this.rule = rule
                ? _.clone(rule)
                : {
                    Type: "Share",
                    Area: 0,
                    ApproverId: "",
                    DateFrom: null,
                    DateTo: null
                };

            for (let i = 0; i < this.approvers.length; i++) {
                let approver = this.approvers[i];
                if (approver.EmployeeNo.toString() == this.rule.ApproverId) {
                    this.showInferiors = approver.IsDirect !== null;
                    break;
                }
            }

            this.dateFrom = this.rule.DateFrom
                ? moment(this.rule.DateFrom).toDate()
                : null;
            this.dateTo = this.rule.DateTo ? moment(this.rule.DateTo).toDate() : null;
        },

        save() {
            this.submitting = true;

            this.rule.DateFrom = moment(this.dateFrom).format("YYYY-MM-DD");
            this.rule.DateTo = moment(this.dateTo).format("YYYY-MM-DD");
            if (this.selectedApprover && this.selectedApprover.EmployeeNo) {
                this.rule.ApproverId = this.selectedApprover ? this.selectedApprover.EmployeeNo : null;
            }

            this.$ajax
                .post("/api/Approvals/SaveRule", this.rule)
                .then(resp => {
                    var data = resp.data;

                    if (!data.success) {
                        return this.showRequestError({
                            resp: resp,
                            title: "Couldn't save the rule"
                        });
                    }

                    var rule = JSON.parse(data.data);

                    // rule.AreaName = this.areas[rule.Area];
                    let isNew = !this.rule.Id;

                    // if (!isNew) {
                    //   let index = this.rules.indexOf(this.rule);
                    //   this.$set(this.rules, index, rule);
                    // } else this.rules.push(rule);

                    this.view = "grid";
                    this.submitting = false;

                    let op = isNew ? "added" : "updated";
                    this.getData();
                    this.addToast({
                        type: "success",
                        title: `Rule is ${op}`,
                        message: `Rule has been successfully ${op}.`
                    });
                })
                .catch(resp => {
                    this.showRequestError({
                        resp,
                        title: "Couldn't save the rule"
                    });
                })
                .then(() => {
                    this.submitting = false;
                });
        },
        delete(rule, cb) {
            this.$ajax.post("/api/Approvals/DeleteRule", {id: rule.Id}).then(resp => {
                let index = this.rules.indexOf(rule);
                this.rules.splice(index, 1);

                this.addToast({
                    type: "success",
                    title: "Rule is deleted",
                    message: "Rule has been successfully deleted."
                });

                if (cb) cb();
            });
        },
        cancel() {
            this.view = "grid";
        },
        toggleInferiors() {
            this.showInferiors = !this.showInferiors;
        }
    },

};
</script>



<style lang="scss">
@use '../styles/custom-select';

.approvals-manager {
    .ui-calendar-display {
        width: 325px !important;
        box-sizing: content-box;
    }

    .alt-approver-stack {
        width: 337px;
        font-size: 0;
        display: inline-block;

        * {
            font-size: 12px;
        }

        .alt-approver-box {
            width: 300px;
            display: inline-block;
            vertical-align: middle;
        }

        .filter-buttons {
            font-size: 0;
            display: inline-block;
            vertical-align: middle;

            .button {
                margin: 0 0 0 2px;
                height: 25px;
                font-size: 14px;
                padding: 5px 7px;
                background: #888888;
                border: 0;
                min-width: 32px;

                &.active {
                    background: #7abd2d;
                }
            }
        }
    }
}

.newui button.ml-2 {
    margin-left: 8px;
}

.rules-wrap {
    overflow-x: auto;
    overflow-y: hidden;
}

.rules-list {
    margin-left: -20px !important;
    margin-right: -20px !important;
}

table tr td.col-buttons {
    white-space: nowrap;
}

</style>
