<script>
export default {
    props: ['data'],
    computed: {
        tel() {
            return typeof this.data.Key == 'string'
            && (this.data.Key.indexOf('Phone') > -1 || this.data.Key.indexOf('Mobile') > -1)
                ? `tel:${this.data.Value}` : null;

            return null;
        },
        email() {
            return typeof this.data.Value == 'string' && this.data.Value.indexOf('@') > -1
                ? `mailto:${this.data.Value}` : null;
        }
    }
}
</script>

<template>
    <div class="field-display">
        <div class="label">{{ data.Label }}</div>
        <a v-if="email" :href="email" class="value">{{ data.Value }}</a>
        <a v-if="!email && tel" :href="tel" class="value">{{ data.Value }}</a>
        <div v-if="!email && !tel" class="value">{{ data.Value }}</div>
    </div>
</template>
