<template>
    <doc-grid :columns="columns" :data="data" :loading="loading" icon="icon-share" title="Shared With Me"></doc-grid>
</template>

<script>
import DocGrid from './grid.vue';
import {icon, name, dateCreated, buttons} from './columns';

export default {
    props: {
        data: Array,
        loading: Boolean
    },
    computed: {
        columns() {
            let dateShared = dateCreated();
            dateShared.header = 'Shared At';

            return [
                icon(),
                name(), {
                    name: 'OwnerName',
                    header: 'Shared By'
                },
                dateShared,
                buttons()
            ];
        }
    },
    components: {
        DocGrid
    }
}
</script>


