import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

const types = {
    TOAST_ADD: 'TOAST_ADD',
    TOAST_CLOSE: 'TOAST_CLOSE',
    REQ_FAILURE: 'REQ_FAILURE'
}

const mutations = {
    [types.TOAST_ADD](state, toast) {
        state.toasts.unshift(toast);
    },
    [types.TOAST_CLOSE](state, toast) {
        let index = state.toasts.indexOf(toast);
        state.toasts.splice(index, 1);
    }
}

const actions = {
    addToast({commit}, toast) {
        commit(types.TOAST_ADD, toast);
    },
    closeToast({commit}, toast) {
        commit(types.TOAST_CLOSE, toast);
    },
    showRequestError({dispatch}, {resp, title}) {
        if (resp.status != '401') {
            dispatch('addToast', {
                type: 'error',
                title,
                message: resp.data.error || 'Server error occured.'
            });
        } else {
            window.location.href = "/Security/Logout";
        }
    }
}

export default new Vuex.Store({
    state: {
        toasts: []
    },
    mutations,
    actions
})
