export default class Task {
    constructor(file) {
        this._file = file;
        this.progress = 0;
        this.status = 'new';
        this.filename = file.name;
        this.data = {};
        this.error = null;
        this.started = false;

        this._eventHandlers = {
            error: [evt => {
                this.status = 'error';
                this.error = 'File failed to upload due to unexpected issue.';
            }],
            load: [evt => {
                if (evt.target.status >= 400) {
                    this._eventHandlers.error.forEach(h => {
                        h(evt);
                    })
                }
                else this.status = 'complete';
            }]
        };
    }
    _addAventHandler(event, handler) {
        let handlers = this._eventHandlers[event];
        if (!handlers) handlers = this._eventHandlers[event] = [];

        handlers.push(handler);
    }
    get file() {
        return this._file;
    }
    start(method, url) {
        this.progress = 0;
        this.error = null;
        this.started = true;
        this._xhr = new XMLHttpRequest();

        var fd = new FormData();

        this._xhr.upload.addEventListener('progress', evt => {
            this.progress = evt.loaded / evt.total;
        });

        for (let event in this._eventHandlers) {
            let handlers = this._eventHandlers[event] || [];
            handlers.forEach(h => {
                this._xhr.addEventListener(event, h);
            });
        }

        this._xhr.open(method, url, true);

        fd.append('file', this.file);
        fd.append('name', this.filename);

        for (let key in this.data) {
            fd.append(key, this.data[key]);
        }

        this.status = 'sending';

        this._xhr.send(fd);
    }
    cancel() {
        this._xhr.abort();
    }
    onError(handler) {
        this._addAventHandler('error', handler);
    }
    onComplete(handler) {
        this._addAventHandler('load', handler);
    }
}
