<script>
    import Selector from './selector.vue';
    import Task from './task';
    import icons from './icons';
    import bowser from 'bowser';

    export default {
        props: {
            url: String,
            accept: null,
            statusFilter: Array,
            maxSizeMB: {
                type: Number,
                default: 20
            },
            canAddTasks: {
                type: Boolean,
                default: true
            },
            method: {
                type: String,
                default: 'POST'
            },
            threads: {
                type: Number,
                default: 1
            },
            tasks: {
                type: Array,
                default() {
                    return [];
                }
            }
        },
        data() {
            return {
                started: false,
                uploading: 0
            }
        },
        computed: {
            queue() {
                return this.filterByStatus(['queued']);
            },
            filtered() {
                if (!this.statusFilter) return this.tasks;
                return this.filterByStatus(this.statusFilter);
            },
            selectorText() {
                let text = 'Click to select files';
                
                if (!bowser.msie && !bowser.msedge) {
                    text += ' or drag and drop them onto this box';
                }

                return text;
            }
        },
        watch: {
            queue(queue) {
                if (!this.started) return;

                let sending = 0;

                this.tasks.forEach(t => {
                    if (t.status == 'sending') sending++;
                });

                for (let i = 0; i < this.threads - sending; i++) {
                    let task = queue.shift();
                    if (task) task.start(this.method, this.url);
                    else {
                        this.started = false;
                        break;
                    };
                }
            }
        },
        methods: {
            filterByStatus(filter) {
                let tasks = [];

                this.tasks.forEach(t => {
                    if (filter.indexOf(t.status) >= 0) {
                        tasks.push(t);
                    }
                });

                return tasks;
            },
            icon(task) {
                let segments = task.file.name.split('.');
                let ext = segments.length > 1 ? segments.pop() : '';
                return icons[ext.toLowerCase()] || 'icon-page';
            },
            upload(task) {
                if (!task) {
                    return this.tasks.forEach(task => {
                        this.upload(task);
                    });
                }

                if (!task.started) {
                    if (task.status == 'error') return;

                    task.onComplete(evt => {
                        if (task.status == 'error') return;
                        this.$emit('task-complete', task, evt.target);
                        this.uploading--;
                        if (!this.uploading) this.$emit('complete');
                    });

                    task.onError(evt => {
                        this.$emit('task-error', task, evt.target);
                        this.uploading--;
                        if (!this.uploading) this.$emit('complete');
                    });
                }

                this.uploading++;

                if (task.status == 'queued' || task.status == 'sending') return;

                this.started = true;
                task.status = 'queued';
            },
            remove(task) {
                let index = this.tasks.indexOf(task);
                this.tasks.splice(index, 1);
            },
            cancel(task) {
                task.cancel();
                this.remove(task);
            },
            validateTask(task) {
                let hasError = false;

                if (typeof this.accept == 'string') hasError = task.file.type != this.accept;
                else if (this.accept instanceof Array) hasError = this.accept.indexOf(task.file.type) < 0;

                if (hasError) {
                    task.status = 'error';
                    task.error = 'File type is not supported.'
                    return false;
                }

                if (task.file.size > this.maxSizeMB * 1024 * 1024) {
                    task.status = 'error';
                    task.error = `File exceeding a maximum size of ${ this.maxSizeMB } MB.`
                    return false;
                }

                return true;
            },
            onFilesSelected(files) {
                for (let i = 0; i < files.length; i++) {
                    let task = new Task(files[i]);
                    this.validateTask(task);
                    this.tasks.push(task);
                }

                this.$emit('tasks-created', this.tasks);
                this.$refs.selector.clear();
            }
        },
        components: {
            Selector
        }
    }
</script>

<template>
    <ul class="file-list">
        <li v-for="(task, index) in filtered" class="file" :class="{ error: task.status == 'error' }">
            <div v-if="task.status == 'sending' || task.status == 'complete'"
                class="progress-mask" :style="{ width: task.progress * 100 + '%' }"></div>
            <div class="layout">
                <span :class="['col', 'col-icon', icon(task)]"></span>
                <div class="col">
                    <div class="name-box">{{ task.filename }}</div>
                    <div v-if="task.error" class="error-box">{{ task.error }}</div>
                </div>
                <div v-if="task.status == 'queued' || task.status == 'sending'" class="col col-progress">
                    <div class="loader-box"></div>
                </div>
                <div v-if="task.status == 'error' && task.started" class="col col-button icon-cw" @click="upload(task)"></div>
                <div v-if="task.status == 'sending'" class="col col-button icon-blocked" @click="cancel(task)"></div>
                <div v-if="task.status == 'new'" class="col col-button icon-trash" @click="remove(task)"></div>
                <div v-if="task.status == 'complete' || task.status=='error'" class="col col-button icon-tick" @click="remove(task)"></div>
            </div>
        </li>
        <selector v-if="canAddTasks" ref="selector" :accept="accept" :allow-multiple="true" @files-selected="onFilesSelected">
            <div class="icon icon-search"></div>
            <div class="text">{{ selectorText }}</div>
        </selector>
    </ul>
</template>

<style lang="scss">
    @mixin filebox {
        border: 1px dashed #aaa;
        width: 100%;
        box-sizing: border-box;
        margin-top: 10px;
    }

    .upload-section {
        li.file {
            @include filebox;
            height: 50px;
            position: relative;

            &.error {
                background-color: #FFCDD2;
            }

            .layout,
            .progress-mask {
                height: 100%;
                position: absolute;
                top: 0;
                left: 0;
                box-sizing: border-box;
            }

            .layout {
                display: table;
                padding: 0 10px;
                width: 100%;
            }

            .error-box {
                font-size: 10px;
                color: #D32F2F;
            }

            .progress-mask {
                background-color: #dcedc8;
                display: inline-block;
            }

            .col {
                display: table-cell;
                vertical-align: middle;
            }

            .col-icon {
                width: 40px;
                font-size: 26px;
            }

            .col-progress {
                width: 30px;
            }

            .col-button {
                font-size: 20px;
                color: #606060;
                cursor: pointer;
                width: 30px;

                &:hover {
                    color: #23588a;
                }
            }
        }

        .file-selector {
            @include filebox;
            cursor: pointer;
            font-size: 0;
            text-align: center;
            padding: 15px;

            .icon {
                font-size: 18px;
                vertical-align: top;
                margin: 0;
                line-height: 25px;
                margin-right: 5px;
                display: inline-block;
            }

            .text {
                font-size: 14px;
                line-height: 25px;
                display: inline-block;
            }
        }

        .section-bottom {
            margin-top: 15px;
        }
    }
</style>
