<template>
    <doc-grid :actions="actions" :columns="columns" :data="data" :loading="loading" class="own-docs"
              icon="icon-archive" title="My Own Documents"></doc-grid>
</template>


<script>
import DocGrid from './grid.vue';
import {icon, name, dateCreated, dateModified, buttons} from './columns';

export default {
    components: {
        DocGrid
    },
    props: {
        data: Array,
        loading: Boolean,
        actions: Array
    },
    computed: {
        columns() {
            let columns = [
                icon(),
                name(),
                dateCreated(),
                dateModified()
            ];

            let colButtons = buttons();
            colButtons.buttons.push({
                mark: " icon small",
                icon: 'icon-trash',
                color: 'red',
                click: (row, start, done) => {
                    start();

                    this.$ajax.post('/api/Documents/Delete', {
                        id: row.Id,
                        category: row.Category
                    })
                        .then(resp => {
                            let index = this.data.indexOf(row);
                            this.data.splice(index, 1);
                        })
                        .catch()
                        .then(() => done());
                }
            });

            columns.push(colButtons);

            return columns;
        }
    },
}
</script>

<style lang="scss">
.own-docs {
    .ui-grid td.col-buttons {
        width: 180px;
    }

    tr td.col-name,
    tr td.col-buttons {
        white-space: nowrap;
    }

    tr td.col-name {
        overflow: hidden;
        text-overflow: ellipsis;
    }

    @media screen and (min-width: 992px) {
        tr td.col-name {
            max-width: 320px;
        }
    }
    @media screen and (max-width: 991px) {
        tr td.col-name {
            max-width: 220px;
            font-size: 12px;
        }
    }
    @media screen and (max-width: 640px) {
        tr td.col-name {
            max-width: 180px;
        }
    }
}
</style>
