<template>

    <div class="dashboard">
        <div class="pagewrapper">
            <div class="pagecontent">
                <div class="contentwrapper">
                    <template>
                        <router-view></router-view>
                        <toaster :toasts="$store.state.toasts" v-on:toast-click="onToastClick" v-on:toast-expired="onToastExpired" :default-timeout="5000" :show-timeout="true"></toaster>
                    </template>
                </div>
            </div>
        </div>
    </div>

<!--    <dashboard config-url="/Config/Dashboard" info-url="/Profile/GetInfo" logout-url="/Security/Logout">-->

<!--    </dashboard>-->
</template>

<script>
import {mapActions} from "vuex";
import Payslips from "./views/payslips";
import Approvals from "./views/approvals";
import Security from "./views/profile/security";
import Documents from "./views/documents";
import BankAccounts from "./views/profile/bank-accounts";
import Search from "./views/search";

if (!('config' in document)) {
    document.config = {};
}
document.config.loaders = {};
document.config.loaders.large = {};
document.config.loaders.large.light = 'data:image/gif;base64,';


export default {
    methods: {
        ...mapActions([
            'closeToast'
        ]),
        onToastExpired(toast) {
            this.closeToast(toast);
        },
        onToastClick(toast) {
            this.closeToast(toast);
            if (toast.onClick) toast.onClick();
        }
    },
    components: {
        Payslips,
        Approvals,
        Security,
        Documents,
        BankAccounts,
        Search
    }
}
</script>

<style lang="scss">
#app {
    font-family: Avenir, Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
}

#nav {
    padding: 30px;

    a {
        font-weight: bold;
        color: #2c3e50;

        &.router-link-exact-active {
            color: #42b983;
        }
    }
}
</style>
