<script>
import Vue from "vue";
import FieldDisplay from "./field-display.vue";
import Separator from "./separator-line.vue";
import YoutubeInput from "./youtube-input.vue";
import Paragraph from "./paragraph.vue";
import Explanation from "./explanation-text.vue";
import AttachFile from "./attach-file.vue";
import Subheading from "./subheading.vue";
import Link from "./url-link.vue";

export default Vue.extend({
    props: ["title", "icon", "data", "loading", "error"],
    components: {
        FieldDisplay,
        Separator,
        YoutubeInput,
        Paragraph,
        Explanation,
        AttachFile,
        Subheading,
        Link
    },
    computed: {
        items() {
            if (this.data) {
                return this.data.filter(item => {
                    return (
                        item.Type === "Separator" ||
                        item.Type === "Subheading" ||
                        item.Type === "Explanation" ||
                        item.Type === "Paragraph" ||
                        item.Type === "YoutubeInput" ||
                        item.Type === "Link" ||
                        item.Value
                    );
                });
            }
        }
    }
});
</script>

<template>
    <tile :icon="icon" :loading="loading" :title="title" class="cf-widget">
        <div v-if="error" class="error-text">{{ error }}</div>
        <div v-else>
            <component
                :is="item.Type"
                v-for="(item, index) in items"
                :key="index"
                :data="item"
                class="item"
            ></component>
        </div>
    </tile>
</template>

<style lang="scss">
.cf-widget {
    .item {
        display: table;
        width: 100%;
        height: 20px;
    }

    .label {
        display: table-cell;
    }

    .value {
        display: table-cell;
        text-align: right;
    }

    .subheading .label {
        font-weight: bold;
    }

    .youtube-input {
        display: block;
        height: auto;
        text-align: center;
    }

    .explanation {
        padding: 10px;
        box-sizing: border-box;
        background: #ccc;
        margin: 0;
    }
}
</style>
