<template>
    <div class="tile-box">
        <div class="tile">
            <div class="tileheader">
                <div class="title-box">
                    <span v-if="icon" :class="'icon small ' + icon"></span>
                    <span class="title">{{ title }}</span>
                    <a href="/Help/Index" target="_blank">
                      <span v-if="help" :class="'icon small ' + help"></span>
                    </a>
                </div>
                <div class="section-nav" v-if="actions && !loading">
                    <ul>
                        <li v-for="a in enabledActions" :class="{ [`u-${a.color}`]: a.color }" @click="onActionClick(a)">
                            <span :class="a.icon"></span>
                            <span>{{ a.label }}</span>
                        </li>
                    </ul>
                </div>
            </div>
            <div v-if="!loading" class="tilebody" :class="{ nopadding: nopadding }">
                <slot></slot>
            </div>
            <div v-if="loading" class="loading-box">
                <img :src="loader">Please wait...
            </div>
        </div>
    </div>
</template>

<script>
    import Vue from 'vue';

    let Tile = Vue.extend({
        props: ['title', 'icon', 'help', 'nopadding', 'loading', 'actions', 'view'],
        data() {
            return {
                loader: document.config.loaders.light
            }
        },
        computed: {
            enabledActions() {
                let actions = [];

                if (this.actions instanceof Array) {
                    this.actions.forEach((a) => {
                        if (a.views && a.views.indexOf(this.view) < 0) return;
                        if (a.enabled != false) actions.push(a);
                    });
                }

                return actions;
            }
        },
        methods: {
            onActionClick(action) {
                if (action.click) action.click.call(this);
            }
        }
    });

    Vue.component('tile', Tile);

    export default Tile;
</script>
