<script>
    import Vue from 'vue';

    export default {
        props: {
            toasts: {
                type: Array,
                default() {
                    return [];
                }
            },
            defaultTimeout: {
                type: Number,
                default: 2000
            },
            showTimeout: Boolean
        },
        data() {
            return {
                ready: false,
                currentId: 1
            }
        },
        watch: {
            toasts() {
                this.stampToasts();
            }
        },
        methods: {
            stampToasts() {
                (this.toasts || []).forEach((t) => {
                    if (!t.id) {
                        t.id = this.currentId++;
                        let hasDuration = typeof t.duration == 'number';
                        if (!hasDuration) this.$set(t, 'duration', this.defaultTimeout);

                        if (t.duration > 0) {
                            t.timeout = setTimeout(() => {
                                if (this.toasts.indexOf(t) >= 0) {
                                    this.$emit('toast-expired', t);
                                }
                            }, t.duration);
                        }
                    }
                });
            }
        },
        created() {
            this.stampToasts();
        },
        mounted() {
            this.ready = true;
        }
    }
</script>

<template>
    <transition-group tag="div" class="toaster" name="fade">
        <div class="toast" :class="[ t.type || 'info' ]" v-for="t in toasts" @click="$emit('toast-click', t)" :key="t.id">
            <div class="toast-inner">
                <div class="toast-icon">
                    <span class="info-icon icon-info"></span>
                    <span class="error-icon icon-blocked"></span>
                    <span class="success-icon icon-tick"></span>
                    <span class="warning-icon icon-warning"></span>
                </div>
                <div class="toast-body">
                    <div class="toast-title" v-if="t.title">{{ t.title }}</div>
                    <div class="toast-message">{{ t.message }}</div>
                </div>
            </div>
            <div class="toast-timeout-bar" v-if="showTimeout">
                <div class="toast-timeout-bar-inner" :class="{ 'full': !ready }" :style="{ transition: 'width ' + t.duration + 'ms linear' }"></div>
            </div>
        </div>
    </transition-group>
</template>

<style lang="scss">
    .toaster {
        position: fixed;
        padding: 10px;
        z-index: 9999;
        top: 5px;
        left: 50%;
        margin-left: -150px;

        .toast {
            color: white;
            width: 300px;
            font-size: 0;
            position: relative;
            opacity: 1;
            transition: opacity .3s linear;
            cursor: pointer;

            .toast-inner {
                padding: 10px;
            }

            &.fade-enter,
            &.fade-leave-active {
                opacity: 0;
            }

            &.fade-enter .toast-timeout-bar-inner {
                width: 100% !important;
            }

            & + .toast {
                margin-top: 10px;
            }

            .toast-timeout-bar {
                height: 5px;

                .toast-timeout-bar-inner {
                    background: black;
                    opacity: 0.2;
                    display: inline-block;
                    height: 100%;
                    width: 0;

                    &.full {
                        width: 100%;
                    }
                }
            }

            .toast-icon {
                display: inline-block;
                vertical-align: top;
                font-size: 30px;
                position: absolute;
                width: 30px;

                & > span {
                    display: none;
                }
            }

            &.info {
                background: #448ccf;

                .info-icon {
                    display: inline-block;
                }
            }

            &.error {
                background: #E53935;
                border: 0;

                .error-icon {
                    display: inline-block;
                }
            }

            &.success {
                background: #689F38;

                .success-icon {
                    display: inline-block;
                }
            }

            &.warning {
                background: #FF9800;

                .warning-icon {
                    display: inline-block;
                }
            }

            .toast-body {
                display: inline-block;
                padding-left: 40px;
                font-size: 12px;
                min-height: 33px;
            }

            .toast-title {
                font-weight: bold;

                & + .toast-message {
                    margin-top: 5px;
                }
            }
        }
    }
</style>
