import axios from 'axios';

if (process.env.VUE_APP_AXIOSHOST && process.env.VUE_APP_LOCAL) {
    // axios.defaults.baseURL = 'http://localhost:3000'
    console.log('axios host is used', process.env.VUE_APP_AXIOSHOST)
    axios.defaults.baseURL = process.env.VUE_APP_AXIOSHOST
}

let routerBase = 'api'

if (process.env.VUE_APP_ISMOCK) {
    console.log('api in mock mode')
    routerBase = ''
}

const approvalsGetDataAPI = `/${routerBase}/Approvals/GetData`
const configStaffSearchAPI = `/${routerBase}/Config/StaffSearch`
const searchAPI = `/${routerBase}/Search`
const getDetails = `/${routerBase}/Search/GetDetails`
const getAdditionalDetails = `/${routerBase}/Search/GetAdditionalDetails`


export default {
    approvalsGetDataAPI,
    configStaffSearchAPI,
    searchAPI,
    getAdditionalDetails,
    getDetails
}