<script>
    export default {
        props: {
            value: Boolean
        },
        watch: {
            state(value) {
                this.checked = value;
            }
        },
        methods: {
            onStateChange() {
                this.$emit('input', !this.value);
            }
        },
        created() {
            this.checked = this.state;
        }
    }
</script>

<template>
    <label class="switch">
      <input type="checkbox" :checked="value" @change="onStateChange">
      <span></span>
    </label>
</template>

<style lang="scss">
    $switch-width: 40px;
    $switch-height: 25px;
    $switch-slider-width: $switch-height - 2px;

    .switch {
        padding: 0 !important;
        width: $switch-width !important;
        display: inline-block;

        & > input {
            position: absolute;
            margin-left: -9999px;
            visibility: hidden;

            & + span {
                display: block;
                position: relative;
                cursor: pointer;
                outline: none;
                user-select: none;
                padding: 2px;
                width: $switch-width;
                height: $switch-height;
                background-color: #dddddd;
                border-radius: $switch-height;
                box-sizing: border-box;
            }

            & + span:before,
            & + span:after {
                display: block;
                position: absolute;
                top: 1px;
                left: 1px;
                bottom: 1px;
                content: "";
            }

            & + span:before {
                right: 1px;
                background-color: #ccc;
                border-radius: $switch-height;
                transition: background 0.4s;
            }

            & + span:after {
                width: $switch-slider-width;
                background-color: #fff;
                border-radius: 100%;
                box-shadow: 0 2px 3px rgba(0, 0, 0, 0.3);
                transition: margin 0.4s;
            }

            &:checked + span:before {
                background-color: #7abd2d;
            }

            &:checked + span:after {
                margin-left: $switch-width - $switch-slider-width;
            }
        }
    }
</style>
