<template>
    <div class="acc-num-box">
        <div class="form-row">
            <label>Account Name:</label>
            <input v-model="data.Name" type="text" />
            <div v-if="nameError" class="name-error">{{ nameError }}</div>
        </div>
        <div class="form-row">
            <label>Account Number:</label>
            <div class="acc-num">
                <input
                    v-model="bankCode"
                    class="bank"
                    maxlength="3"
                    type="text"
                    @input="onNumberChange()"
                />
                <span class="sepr">-</span>
                <input
                    v-model="branchCode"
                    class="branch"
                    maxlength="5"
                    type="text"
                    @input="onNumberChange()"
                />
                <span class="sepr">-</span>
                <input
                    v-model="accountNumber"
                    class="account"
                    type="text"
                    v-bind:maxlength="[this.showSuffix ? 7 : 10]"
                    @input="onNumberChange()"
                    v-on:blur="handleAccountNumber()"
                />
                <span v-if="showSuffix" class="sepr">-</span>
                <input
                    v-if="showSuffix"
                    v-model="accountSuffix"
                    class="suffix"
                    maxlength="3"
                    type="text"
                    @input="onNumberChange()"
                />
                <span :class="numberStatusIcon" class="validation-icon"></span>
            </div>
            <div v-if="bankName" class="acc-num-details">
                <div v-if="bankName" class="bank-name">{{ bankName }}</div>
                <div v-if="branchName" class="branch-name">
                    {{ branchName }}
                </div>
            </div>
        </div>
        <div v-if="showAmount" class="form-row">
            <label>Amount:</label>
            <div class="acc-amount-box">
                <input v-model="amount" class="amount-value" type="text" />
                <select v-model="amountType" class="amount-type">
                    <option value="$">Dollars</option>
                    <option value="%">Percent</option>
                </select>
            </div>
            <div v-if="amountError" class="amount-error">{{ amountError }}</div>
        </div>
    </div>
</template>

<script>
import { debounce } from "lodash";

export default {
    props: {
        data: Object,
        showAmount: Boolean,
        country: String,
        validationUrl: String,
    },
    data() {
        return {
            req: null,
            numberValid: null,
            bankCode: "",
            bankName: "",
            branchCode: "",
            branchName: "",
            accountNumber: "",
            accountSuffix: "",
            amount: 0,
            amountType: "$",
            amountError: null,
            nameError: null,
        };
    },
    computed: {
        numberStatusIcon() {
            switch (this.numberValid) {
                case null:
                    return "icon-blocked";
                case true:
                    return "icon-tick";
                case false:
                    return "icon-cross";
            }
        },
        showSuffix() {
            let country = (this.country || "").toUpperCase();
            return country == "N" || country == "NZ";
        },
    },
    watch: {
        amount(amount) {
            this.amountError = null;
            this.calcAmount();
        },
        amountType() {
            this.amountError = null;
            this.calcAmount();
        },
    },
    methods: {
        calcAmount() {
            this.data.Amount = this.amount;
            if (this.amountType == "%") this.data.Amount /= 100;
        },
        validate() {
            this.nameError = null;
            this.amountError = null;

            let result = true;

            if (this.data.Name && this.data.Name.length > 30) {
                this.nameError =
                    "Only 30 characters (including spaces) allowed.";
                result = false;
            }

            if (this.showAmount && this.amount) {
                let amount = parseFloat(this.amount);

                let amountValid =
                    /^-?[1-9]\d*(\.\d+)?$/.test(this.amount) &&
                    !isNaN(amount) &&
                    amount >= 0;

                if (amountValid) {
                    let parts = this.amount.toString().split(".");
                    amountValid = !parts[1] || parts[1].length <= 2;
                }

                if (!amountValid)
                    this.amountError =
                        "Amount must be a positive number with 2 decimal places.";

                if (amountValid && this.amountType == "%") {
                    amountValid = amount <= 100;
                    if (!amountValid)
                        this.amountError =
                            "Amount must be a number between 0 and 100";
                }

                result &= amountValid;
            }

            result &= !!this.numberValid;

            return result;
        },
        validateNumber() {
            if (this.req) this.req.cancel();

            this.req = this.$ajax.CancelToken.source();

            const bankAccount = this.data.Number.replace(/-+$/, "");

            this.$ajax
                .post(
                    this.validationUrl,
                    {
                        country: this.country,
                        number: bankAccount,
                    },
                    {
                        cancelToken: this.req.token,
                    }
                )
                .then((resp) => {
                    this.numberValid = resp.data.IsValid;
                    this.bankName = resp.data.BankName;
                    this.branchName = resp.data.BranchName;
                });
        },
        onNumberChange() {
            this.numberValid = null;
            this.data.Number = `${this.bankCode}-${this.branchCode}-${this.accountNumber}-${this.accountSuffix}`;

            // if fields are empty do not trigger validate
            if (
                !this.bankCode &&
                !this.branchCode &&
                !this.accountNumber &&
                !this.accountSuffix
            ) {
                return;
            }
            this.validateNumber();
        },
        handleAccountNumber() {
            if (this.showSuffix) {
                this.accountNumber = this.accountNumber.padStart(7, "0");
            }
        },
    },
    created() {
        let sections = (this.data.Number || "").split("-");
        this.bankCode = sections[0] || "";
        this.branchCode = sections[1] || "";
        this.accountNumber = sections[2] || "";
        this.accountSuffix = sections[3] || "";
        this.amountType = this.data.Amount <= 1 ? "%" : "$";

        this.amount = this.data.Amount;
        if (this.amountType == "%") this.amount *= 100;

        // if fields are empty do not trigger validate
        if (
            !this.bankCode &&
            !this.branchCode &&
            !this.accountNumber &&
            !this.accountSuffix
        ) {
            return;
        }

        this.validateNumber();
        this.validateNumber = debounce(this.validateNumber, 500);
        this.calcAmount = debounce(this.calcAmount, 500);
    },
};
</script>

<style lang="scss">
.acc-num-box {
    .acc-num {
        display: inline-block;

        input[type="text"] {
            text-align: center !important;
        }

        .bank {
            width: 38px !important;
        }

        .branch {
            width: 51px !important;
        }

        .account {
            width: 110px !important;
        }

        .suffix {
            width: 32px !important;
        }

        .validation-icon {
            font-size: 14px;
            display: inline-block;
            height: 27px;
            line-height: 27px;
            vertical-align: top;

            &.icon-tick {
                color: #7abd2d;
            }

            &.icon-cross {
                color: red;
            }
        }
    }

    .acc-num-details {
        font-style: italic;
        color: #999999;
        padding-left: 180px;
        padding-top: 5px;

        .branch-name {
            margin-top: 5px;
        }
    }

    .acc-amount-box {
        font-size: 0;
        @media screen and (min-width: 768px) {
            display: inline-block;
        }

        @media screen and (max-width: 767px) {
            display: flex;
        }

        & * {
            font-size: 12px;
        }

        .amount-value {
            width: 230px !important;
            box-sizing: border-box;
        }

        .amount-type {
            padding: 0 5px !important;
            height: 25px;
            margin-left: 7px !important;
            width: 100px !important;
        }
    }

    .amount-error,
    .name-error {
        color: red;
        padding-left: 180px;
        padding-top: 5px;
        font-size: 10px;
    }
}
</style>
