<template>
    <tile :actions="actions" :columns="columns" :icon="icon" :loading="loading" :nopadding="true" :title="title"
          class="doc-grid">
        <div class="section">
            <div class="section-table nomargin">
                <grid :columns="columns" :data="data" :descending="true" :row-marker="markRow"
                      key-prop="Id" load-sorted-by="DateCreated" no-data-text=""></grid>
            </div>
        </div>
    </tile>
</template>

<script>
export default {
    props: {
        data: Array,
        title: String,
        icon: String,
        actions: Array,
        columns: Array,
        loading: Boolean
    },
    methods: {
        markRow(row) {
            return row.IsNew ? 'new' : '';
        }
    }
}
</script>


<style lang="scss">
.doc-grid {
    tr.new td {
        font-weight: bold !important;
    }

    tr td.col-name,
    tr td.col-buttons {
        white-space: nowrap;
    }

    tr td.col-name {
        overflow: hidden;
        text-overflow: ellipsis;
    }

    @media screen and (min-width: 992px) {
        tr td.col-name {
            max-width: 320px;
        }
    }
    @media screen and (max-width: 991px) {
        tr td.col-name {
            max-width: 220px;
            font-size: 12px !important;
        }
    }
    @media screen and (max-width: 640px) {
        tr td.col-name {
            max-width: 180px;
        }
    }
}

@media screen and (max-width: 767px) {
    .newui .section {
        .section-table {
            overflow-x: auto;
            overflow-y: hidden;
        }

        table {
            min-width: 740px;
        }
    }
}
</style>
