<template>
    <div class="custom-select aff-v1" tabindex="1"  ref="selectRef">
        <div class="custom-select-selected">
            <input class="custom-select-input" ref="selectInputRef"
                   v-model="selectSearchInput" @keyup="updateSearchList">
        </div>
        <div class="custom-select-items">
            <div class="custom-select-item" ref="selectDropdownRef"
                 v-for="(option, index) in dropDownList" :key="index"
                 :tabindex="index + 99"
                 v-bind:class="{'targeting': option.targeting}"
                 @click="chooseOption($event, option)">
                <div class="cell-config">
                    {{ option.name }}
                </div>
            </div>
        </div>
    </div>
</template>

<script>
// import {defineComponent, ref} from "vue";
import _ from "lodash";

export default {
    name: "AutoComplete",
    props: {
        inputList: {
            type: Array
        },
        optionName: {
            type: String
        },
        defaultSelected: {
            type: String
        }
    },
    data() {
        return {
            // selectSearchInput: false,
            editing: false,
            targetingIndex: null,
            // downDownList: [],
            originList: [],
            dropDownList: [],
            selectSearchInput: []
        }

    },
    mounted() {
        if (!this.optionName) {
            // todo: treat as string array
            console.error("need object, optionName is missing")
        } else {
            // todo: treat as object array
            // console.log('inputLIst', props.inputList)
            // console.log('inputLIst', props.optionName)
        }

        this.populateList(this.inputList)
    },
    watch: {
        inputList: function (n) {
            this.populateList(n)
        }
    },

    methods: {
        populateList(inputList) {
            this.originList = inputList.map((obj, index) => {
                return {
                    targeting: false,
                    name: obj[this.optionName],
                    index: index
                }
            })

            this.dropDownList = this.originList.slice();
            if (this.defaultSelected) {
                this.selectSearchInput = this.defaultSelected
            } else {
                this.selectSearchInput = '';
            }

        },
        updateSearchList(event) {
            if (event.key === "ArrowDown") {
                this.moveTargetDown();
                return
            } else if (event.key === "ArrowUp") {
                this.moveTargetUp();
                return
            } else if (event.key === "Enter") {
                if (this.targetingIndex) {
                    this.selectFromTarget();
                    return
                }
            }

            this.dropDownList = this.originList.filter((o) => {
                o.targeting = false;
                return o.name.toLowerCase().indexOf(this.selectSearchInput.toLowerCase()) > -1
            })
        },

        moveTargetDown() {
            if (this.targetingIndex !== null) {
                this.dropDownList[this.targetingIndex].targeting = false;
                if (this.targetingIndex + 1 < this.dropDownList.length) {
                    this.targetingIndex++
                    this.dropDownList[this.targetingIndex].targeting = true;
                } else if (this.targetingIndex + 1 === this.dropDownList.length) {
                    this.targetingIndex = 0
                    this.dropDownList[this.targetingIndex].targeting = true;
                } else {
                    this.targetingIndex = 0
                    this.dropDownList[this.targetingIndex].targeting = true;
                }
            } else {
                this.targetingIndex = 0
                this.dropDownList[this.targetingIndex].targeting = true;
            }
        },

        moveTargetUp() {
            if (this.targetingIndex !== null) {
                this.dropDownList[this.targetingIndex].targeting = false;
                if (this.targetingIndex - 1 >= 0) {
                    this.targetingIndex--
                    this.dropDownList[this.targetingIndex].targeting = true;
                } else if (this.targetingIndex - 1 < 0) {
                    this.targetingIndex = this.dropDownList.length - 1;
                    this.dropDownList[this.targetingIndex].targeting = true;
                } else {
                    this.targetingIndex = 0
                    this.dropDownList[this.targetingIndex].targeting = true;
                }
            } else {
                this.targetingIndex = 0
                this.dropDownList[this.targetingIndex].targeting = true;
            }
        },

        selectFromTarget() {
            this.selectSearchInput = this.dropDownList[this.targetingIndex].name
            this.$emit('selected', this.inputList[this.targetingIndex])
        },

        chooseOption($event, option) {
            this.selectSearchInput = option.name
            this.$emit('selected', this.inputList[option.index])

            $event.target.blur();
        }
    }
}
</script>

<style lang="scss" scoped>
$grey: #475062;
$aff-dark-blue: #1268B3;
$aff-blue: #00AEEF;
$dark-blue-app: #344356;

.custom-select {
    font-size: 12px;
}

.custom-select-selected {
    min-height: 24px;
    padding: 4px 1.5em 4px 0.2em;
    background-color: #f7f9fe;

}

.custom-select-selected:after {
    top: 8px;
    right: 0;
}

.custom-select:focus-within {
    .custom-select-selected {
        border: none;
        border-radius: 0;
        border-bottom: 1px solid $aff-dark-blue;
    }

    .custom-select-selected:after {
        transform: rotate(180deg);
        top: 4px;
    }
}

.custom-select-items {
    background: white;
}

.custom-select-item {
    color: $dark-blue-app;
    // padding-left: 1em;
    cursor: pointer;
    user-select: none;
    padding: 4px 1em;
}

.custom-select-item:hover {
    color: $aff-dark-blue;
    background-color: lighten($aff-blue, 50%);
}

.custom-select-input {
    border: none;
    background: transparent;
}

.cell-config {
    pointer-events: none;
}

.targeting {
    background-color: $aff-dark-blue;
    color: white;
}
.aff-v1 {
    .custom-select-selected {
        border-bottom: 1px solid transparent;
    }

    &:focus-within .custom-select-selected:after {
        top: 10px;
    }

    .custom-select-selected:after {
        right: 12px;
        top: 15px;
        border-color: black transparent transparent transparent;
    }

    .custom-select-input {
        padding: 5px;
        width: 100%;
        background: transparent;
    }
}

</style>