<template>
    <tile class="change-password w-100" icon="icon-lock" title="Change Password">
        <div class="section">
            <div class="section-body">
                <div class="default-form">
                    <div class="form-row">
                        <label>Current Password:</label>
                        <input v-model="current" type="password"/>
                    </div>
                    <div class="form-row">
                        <label>New Password:</label>
                        <input v-model="next" type="password"/>
                    </div>
                    <div class="form-row">
                        <label>Confirm Password:</label>
                        <input v-model="confirm" type="password"/>
                    </div>
                    <div v-if="submitting" class="form-row row-bottom loader-box"></div>
                    <div v-else class="form-row row-bottom">
                        <button class="button green" @click="save()"><span
                            class="b-icon icon-save"></span><span>Save</span></button>
                    </div>
                </div>
            </div>
        </div>
    </tile>
</template>

<script>
import {mapActions} from 'vuex';

export default {
    data() {
        return {
            submitting: false,
            current: '',
            next: '',
            confirm: '',
            error: ''
        }
    },
    methods: {
        ...mapActions([
            'addToast',
            'showRequestError'
        ]),
        save() {
            if (this.validate()) {
                this.submitting = true;

                this.$ajax.post('/api/Security/UpdatePassword', {
                    CurrentPassword: this.current,
                    NewPassword: this.next
                })
                    .then(resp => {
                        if (resp.data.Success) {
                            this.current = '';
                            this.next = '';
                            this.confirm = '';
                            this.error = '';

                            this.addToast({
                                type: 'success',
                                title: 'Password is changed',
                                message: 'Password has been successfully changed.'
                            })
                        } else this.addToast({
                            type: 'error',
                            message: resp.data.Error,
                            title: "Couldn't change the password"
                        });
                    })
                    .catch()
                    .then(() => {
                        this.submitting = false;
                    });
            }
        },
        validate() {
            let error = null;

            if (!this.current) error = "Current password is required."
            else if (!this.next) error = "New password is not entered."
            else if (!this.confirm || this.next != this.confirm) {
                error = `Password is not confirmed${this.confirm ? ' correctly' : ''}.`;
            }

            if (error) this.addToast({
                type: 'error',
                title: "Password validation failed",
                message: error
            });

            return !error;
        }
    }
}
</script>

