import Vue from 'vue'
import VueRouter from 'vue-router'
import Approvals from '../views/approvals'
import Documents from '../views/documents'
import Payslips from '../views/payslips'
import BankAccounts from '../views/profile/bank-accounts'
import Search from '../views/search'
import Security from '../views/profile/security'

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        name: 'Home',
        component: Security
    },
    {
        path: '/Approvals',
        name: 'Approvals',
        component: Approvals
    },
    {
        path: '/Documents',
        name: 'Documents',
        component: Documents
    },
    {
        path: '/Payslips',
        name: 'Payslips',
        component: Payslips
    },
    {
        path: '/Profile/BankAccounts',
        name: 'BankAccounts',
        component: BankAccounts
    },
    {
        path: '/Profile/Security',
        name: 'ProfileSecurity',
        component: Security
    },
    {
        path: '/Profile/Approvals',
        name: 'ProfileApprovals',
        component: Approvals
    },
    // seem auto redirect in chtml
    // {
    //     path: '/Profile/PeopleStreme',
    //     name: 'Documents',
    //     component: PeopleStreme
    // },
    {
        path: '/Search',
        name: 'Search',
        component: Search
    },
    {
        path: '/Security',
        name: 'Security',
        component: Security
    }
]

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes
})

export default router
