<script>
    import Vue from 'vue';

    export default {
        props: {
            accept: null,
            allowMultiple: Boolean
        },
        computed: {
            acceptTypes() {
                if (this.accept instanceof Array) return this.accept.join(',');
                else if (this.accept instanceof String) return this.accept;
                else return null;
            }
        },
        methods: {
            clear() {
                this.$refs.form.reset();
            },
            onSelected() {
                this.$emit('files-selected', this.$refs.selector.files);
            }
        }
    }
</script>

<template>
    <form class="file-selector" ref="form">
        <slot></slot>
        <input type="file" ref="selector" :accept="acceptTypes" :multiple="allowMultiple"
            title="Select files" @change="onSelected" />
    </form>
</template>

<style lang="scss" scoped>
    .file-selector {
        position: relative;
        display: inline-block;

        input[type="file"] {
            position: absolute;
            display: inline-block;
            width: 100%;
            height: 100%;
            opacity: 0.01;
            top: 0;
            left: 0;
            cursor: pointer;
        }
    }
</style>
