<template>
    <doc-grid
        :columns="columns"
        :data="data"
        :loading="loading"
        class="company-docs"
        icon="icon-briefcase"
        title="Company Documents">
    </doc-grid>
</template>

<script>
import Vue from "vue";
import DocGrid from "./grid.vue";
import eventhub from "./eventhub";
import {icon, name, dateCreated, dateModified, buttons} from "./columns";

function acceptOrDeclineDocument(accepted, row, start, done) {
    start();

    Vue.ajax
        .post("/Documents/AcceptOrDecline", {
            id: row.Id,
            category: row.Category,
            value: accepted
        })
        .then(resp => {
            row.IsNew = false;
            row.IsAccepted = accepted;
        })
        .catch()
        .then(() => done());
}

let colButtons = buttons();

colButtons.mark = row => {
    if (row.AcceptanceRequired) {
        if (row.IsAccepted === null) {
            return row.IsDownloaded ? "acceptance-required" : "not-downloaded";
        } else return row.IsAccepted ? "accepted" : "declined";
    }
    return "";
};

colButtons.buttons.unshift({
    color: "blue",
    icon: "icon-info",
    mark: "btn-show-info",
    click: (row, start, done) => {
        eventhub.$emit("acceptance-required", {
            text: row.AcceptanceText,
            accept: () => {
                acceptOrDeclineDocument(true, row, start, done);
            },
            decline: () => {
                acceptOrDeclineDocument(false, row, start, done);
            }
        });
    }
});

export default {
    props: {
        data: Array,
        loading: Boolean
    },
    computed: {
        columns() {
            return [icon(), name(), dateCreated(), dateModified(), colButtons];
        }
    },
    components: {
        DocGrid
    }
};
</script>

<style lang="scss">
.company-docs {
    .col-name {
        .col-content {
            span {
                vertical-align: top;
            }

            line-height: 16px;
            display: inline-block;
        }
    }

    .col-buttons {
        .btn-show-info {
            display: none;
        }

        &.not-downloaded,
        &.acceptance-required {
            .btn-toggle-state {
                display: none;
            }
        }

        &.accepted,
        &.declined,
        &.acceptance-required {
            .btn-show-info {
                display: inline-block;
            }
        }
    }

    .acceptance-icon {
        margin: 0 5px 0 0;
        font-size: 16px;
        display: inline-block;

        &.icon-thumbs-up {
            color: #8bc34a;
        }

        &.icon-thumbs-down {
            color: #f44336;
        }

        &.icon-warning {
            color: #ff9800;
        }
    }

    .acceptance-text {
        &.accepted {
            color: #689f38;
        }

        &.declined {
            color: #f44336;
        }
    }
}
</style>
